(function() {
  'use strict';
  /**
   * @description Este modulo toma como premisa que los componentes se puede actualizar en forma independiente.
   * Es decir que pueden hacer un pedido nuevo al servidor y actualizar solo su area.
   * Estas operaciones tiene basicamente dos propositos uno es cambiar la vista actual, como por ejemplo cuando un
   * Formulario pasa de la vista Detail a Edit.
   * O bien si se quiere volver a renderizar el componente, por que los valores que muestran estan desactualizados,
   * Como ser la cantidad de items del carrito a partir de un add to cart.
   * @author Sebastian Claros <sebastian.claros@fielo.com>
   * @version 1
   * @exports fielo/components
   * @see  fielo
   */
  var module = (function() {
    // private
    var version = '1';
    var name = 'components';
    var urlPath = '/';
    // Registro el NS
    var ns_ = fieloUtils.site.FieloSite.ns;
    if (fieloUtils.site.FieloSite.sitePath_ !== '') {
      urlPath += fieloUtils.site.FieloSite.sitePath_ + '/';
    }
    if (ns_ !== '') {
      urlPath += ns_ + '__';
    }

    /**
     * Evento que dispara cuando el formulario empieza una remote action
     */
    function ajaxStart() {
      fieloUtils.spinner.FieloSpinner.show();
    }

    /**
     * Evento que dispara cuando el formulario termina una remote action
     */
    function ajaxStop() {
      fieloUtils.spinner.FieloSpinner.hide();
    }

    /**
     * Recarga el componente en su parent con la nueva vista
     *
     * @param  {HTMLelement} parentElement - Elemento HTML que contiene al item a cargar
     * @param  {HTMLelement} beforeElement - Elemento HTML que es hermano de  parentElement
     * @param  {string} url - URL al cual se hace la peticin ajaz
     */
    function load(parentElement, beforeElement, url) {
      ajaxStart(parentElement);
      fieloUtils.ajax(url, false, function(body, status) {
        if (status === 200) {
          try {
            if (
              typeof beforeElement !== 'undefined' &&
              beforeElement !== null
            ) {
              [].forEach.call(body.children, function(element) {
                parentElement.insertBefore(element, beforeElement);
              });
            } else {
              [].forEach.call(body.children, function(element) {
                parentElement.appendChild(element);
              });
            }

            oldLoader.dispatchEvent({
              type: 'DOMContentReloaded',
              target: parentElement,
              module: 'components'
            });
            ajaxStop(parentElement);
          } catch (e) {
            ajaxStop(parentElement);
            throw (e);
          }
        } else {
          ajaxStop(parentElement);
          throw new Error(
            'There was an error loding the content: code error ' +
            status,
            'components.js'
          );
        }
      });
    }

    return {
      /**
       * @method
       * @summary Retorna el nombre del mdulo
       * @return {string} Nombre del mdulo
       */
      getName: function() {
        return name;
      },

      /**
       * @method
       * @summary Retorna la versin del mdulo
       * @return {string} Versin del mdulo
       */

      getVersion: function() {
        return version;
      },

      /**
       * @method
       * @summary Carga la vista real del contenedor del componente
       * @param {DOMelement} component - Elemento que contiene el componente de Fielo
       */

      refreshView: function(component) {
        var componentId = component.getAttribute('data-componentid');
        var recordParameter = component.getAttribute('data-parameter-name');
        var sObjectInput = component.querySelector('input[name="SObject.id"]');
        var recordId = null;
        var urlToRequest = null;
        if (sObjectInput) {
          recordId = sObjectInput.getAttribute('value');
        }

        if (componentId === null) {
          throw new Error(
            'Cannot get component id',
            'components.js'
          );
        } else {
          urlToRequest = urlPath + 'Component?componentId=' + componentId;
          if (recordId && recordParameter) {
            urlToRequest += '&' + recordParameter + '=' + recordId;
          }
        }

        var background = document.getElementById(componentId + '_background');
        if (background !== null) {
          background.parentNode.removeChild(background);
        }
        var componentParent = component.parentNode;
        var componentSibling = component.nextSibling;
        componentParent.removeChild(component);
        load(
          componentParent,
          componentSibling,
          urlToRequest
        );
      },

      /**
       * @method
       * @summary Vuelve a la vista real del componente.
       * Tiene que ser llamado luego del mtodo switchView para evitar la
       * llamada ajax, sino es lo mismo que refreshView
       * @param {DOMelement} component - Elemento que contiene el componente de Fielo
       */

      switchBack: function(component) {
        var componentId = component.getAttribute('data-componentid');
        var background = document.getElementById(componentId + '_background');
        var componentParent = component.parentNode;
        var recordParameter = component.getAttribute('data-parameter-name');
        var sObjectInput = component.querySelector('input[name="SObject.id"]');
        var recordId = null;
        var urlToRequest = null;
        if (sObjectInput) {
          recordId = sObjectInput.getAttribute('value');
        }
        if (componentId === null) {
          throw new Error(
            'Cannot get component id',
            'components.js'
          );
        } else {
          // Si existe el component (lo esperado) lo elimina
          componentParent.removeChild(component);

          urlToRequest = urlPath + 'Component?componentId=' + componentId;
          if (recordId && recordParameter) {
            urlToRequest += '&' + recordParameter + '=' + recordId;
          }
        }

        if (background === null) {
          // sino existe background refresca  el component
          load(componentParent, null, urlToRequest);
        } else {
          // Si existe el background lo muestra
          background.classList.remove('hidden');
          background.setAttribute('id', componentId + '_container');
          // componentParent.appendChild(background);
        }
      },

      /**
       * @method
       * @summary Carga una nueva vista del contenedor del componente
       * sin perder el actual, el cual lo oculta.
       * @param {DOMelement} component - Elemento que contiene el componente de Fielo
       * @param {string} view - Vista a utilizar
       */

      switchView: function(component, view) {
        var componentParent = component.parentNode;
        var componentSibling = component.nextSibling;
        var componentId = component.getAttribute('data-componentid');
        var recordParameter = component.getAttribute('data-parameter-name');
        var sObjectInput = component.querySelector('input[name="SObject.id"]');
        var recordId = null;
        var urlToRequest = null;
        if (sObjectInput) {
          recordId = sObjectInput.getAttribute('value');
        }

        if (componentId === null) {
          throw new Error(
            'Cannot get component id',
            'components.js'
          );
        } else {
          // Si existe el component (lo esperado) lo elimina
          urlToRequest = urlPath + 'Component?componentId=' + componentId;
          if (recordId && recordParameter) {
            urlToRequest += '&' + recordParameter + '=' + recordId;
          }
        }

        var background = document.getElementById(componentId + '_background');
        // Si existe un background lo elimina
        if (background !== null) {
          background.parentNode.removeChild(background);
        }
        // toma el container lo oculta, le cambia el id con un sufijo 'background'
        component.classList.add('hidden');
        component.setAttribute('id', componentId + '_background');
        // Toma la posicion del componente dentro del padre y lo reparenta (al padre de su padre)
        // componentParent.parentNode.appendChild(component);
        // recarga el componente en su parent con la nueva vista
        load(
          componentParent,
          componentSibling,
          urlToRequest + '&view=' + view
        );
      },

      /**
       * @method
       * @summary Carga una nueva vista de layout para el componente en
       * el contenedor del componente
       * @param {DOMelement} component - Elemento que contiene el componente de Fielo
       * @param {string} view - Vista a utilizar
       */

      changeView: function(component, view) {
        var componentId = component.getAttribute('data-componentid');
        var urlToRequest = null;
        if (componentId === null) {
          throw new Error(
            'Cannot get component id',
            'components.js'
          );
        } else {
          // Si existe el component (lo esperado) lo elimina
          urlToRequest = urlPath + 'Component?componentId=' + componentId;
        }
        var componentParent = component.parentNode;
        var componentSibling = component.nextSibling;
        componentParent.removeChild(component);
        load(
          componentParent,
          componentSibling,
          urlToRequest + '&view=' + view
        );
      }
    };
  })();

  oldLoader.loadModule(module);
})();
