(function() {
  'use strict';

  /**
   * @description Mdulo que inicializa los formView
   * @author Sebastian Claros <sebastian.claros@fielo.com>
   * @version 1
   * @exports fielo/formView
   * @see  fielo
   */

  var module = (function() {
    // private
    var version = '1';
    var name = 'formView';

     /**
      * Agrega los eventos de onclick en los Botones Cancel, Edit
      * @param {HRMLelement} detail - component Id of Fielo
      * @private
      */
    function contentLoadedDetail(detail) {
      // Agrega el evento onclick al boton edit
      var edit = detail.querySelector('.fielo-button__edit');
      if (edit) {
        edit.addEventListener('click', function() {
          oldLoader.components.switchView(detail, 'Edit');
        });
      }
      edit = undefined;
    }
    return {

      /**
       * @method
       * @summary Retorna el nombre del mdulo
       * @return {string} Nombre del mdulo
       */
      getName: function() {
        return name;
      },

      /**
       * @method
       * @summary Retorna la versin del mdulo
       * @return {string} Versin del mdulo
       */

      getVersion: function() {
        return version;
      },

      /**
       * @method
       * @summary Recarga la vista del componente
       * @param {object} event - evento a analizar
       */
      refreshDetail: function(event) {
        var details = event.target.querySelectorAll(
          '.fielo-form-view,.fielo-content[data-remoteobject=' +
          event.object + ']'
        );
        for (var i = 0, max = details.length; i < max; i++) {
          oldLoader.components.refreshView(details[i]);
        }
      },

      /**
       * @method
       * @summary Inicializa los details y registra los eventos
       * DOMContentLoaded y DOMContentReloaded
       */
      initialize: function() {
        oldLoader.addEventListener(
          'DOMContentLoaded',
          this.contentLoaded.bind(this)
        );

        oldLoader.addEventListener(
          'DOMContentReloaded',
          this.contentLoaded.bind(this)
        );

        oldLoader
          .addEventListener('ObjectChange', this.refreshDetail.bind(this));
      },

      /**
       * @method
       * @summary Inicializa  los elementos de los formularios
       * de todas las vistas de formulario
       *
       * @param {object} event - evento a analizar
       */
      contentLoaded: function(event) {
        var details = event.target
          .querySelectorAll('.fielo-form-view.fielo-js-upgrade');
        var i;
        var max;
        for (i = 0, max = details.length; i < max; i++) {
          contentLoadedDetail(details[i], event.type === 'DOMContentReloaded');
        }
        for (i = 0, max = details.length; i < max; i++) {
          details[0].classList.remove('fielo-js-upgrade');
        }
        details = undefined;
      }
    };
  })();

  oldLoader.loadModule(module);
})();
