/**
 * @description Librera de utilidades de Fielo
 *
 * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
 * @author Sebastian Claros <sebastian.claros@fielo.com>
 * @version 2
  */

var fieloUtils = (function() {
  'use strict';

  // Return an object exposed to the public
  return {
    debug: {
     /**
     * @method
     * @summary Muestra la informacin del error.
     * @param {string} message - Mensaje a enviar por consola
     * @param {string} fileName - Nombre del archivo donde se produce el error.
     *
     * @public
     */
      log: function(message, fileName) {
        message = message || 'Message not specified';
        fileName = fileName || 'File name not specified';

        throw new Error(message, fileName);
      }
    },

    /**
     * @method
     * @summary Obtiene el contenido de una url
     * @param  {string} url - url a la cual hacer el ajax
     */
    getUrlContent: function(url) {
      var xmlhttp = new XMLHttpRequest();
      xmlhttp.open('GET', url);
      xmlhttp.send();
      xmlhttp.onreadystatechange = function() {
        if (xmlhttp.readyState === XMLHttpRequest.DONE) {
          if (xmlhttp.status === 200) {
            return xmlhttp.responseText;
          }
          return 'There was an error loding the content: code error ' +
            xmlhttp.status;
        }
      };
    },

    /**
     * @method
     * @summary Levanta una peticin ajax
     *
     * @param {string}    url        URL a la cual se realiza la peticin
     * @param {boolean}   asyncMode  Modo de la peticin ajax
     * @param {function}  callback   Funcin a ejecutar al terminar el ajax
     * @returns Ejecuta el callback teniendo como parmetros a content y xmlhttp.status
     */

    ajax: function(url, asyncMode, callback) {
      var xmlhttp = new XMLHttpRequest();
      xmlhttp.onreadystatechange = function() {
        if (xmlhttp.readyState === XMLHttpRequest.DONE) {
          var content = null;
          if (xmlhttp.status === 200) {
            var parser = new DOMParser();
            var xmlDoc = parser.parseFromString(
              xmlhttp.responseText,
              'text/html'
            );
            content = xmlDoc.getElementsByTagName('body')[0];
          }
          callback(content, xmlhttp.status);
        }
      };
      xmlhttp.open('GET', url, asyncMode);
      xmlhttp.send();
    },

     /**
      * @method
      * @summary Decodifica los caracteres a entidades correctas
      *
      * @param {string} text String adecodificar
      * @return {string} Texto decodificado
      */
    decodeHTMLEntities: function(text) {
      var entities = [
        ['apos', '\''],
        ['amp', '&'],
        ['lt', '<'],
        ['gt', '>']
      ];

      for (var i = 0, max = entities.length; i < max; ++i) {
        text = text.replace(
          new RegExp('&' + entities[i][0] + ';', 'g'), entities[i][
            1]
        );
      }

      return text;
    },

    /**
     * @method
     * @summary Remueve un nodo HTML
     *
     * @param {DOMelement} element Nodo HTML a remover
     */
    removeChilds: function(element) {
      while (element.lastChild) {
        element.removeChild(element.lastChild);
      }
    },

    /**
     * @method
     * @summary Devuelve el elemento padre<br>
     *
     * Obtenido de {@link http://gomakethings.com/climbing-up-and-down-the-dom-tree-with-vanilla-javascript/}
     * @param {DOMelement} element  Elemento HTML al cual se busca el padre
     * @param {string} selector Selector CSS que indica cul tiene que ser su padre
     * @return {DOMelement} Retorna el elemento padre
     */

    getParentUntil: function(element, selector) {
      var firstChar = selector.charAt(0);
      var search;
      switch (firstChar) {
        case '.':
          // Si el selector es .XXXX => Busca si hay un ansestro que contenga la clase XXX
          for (
            search = selector.substr(1); element && element !==
            document; element = element.parentNode
          ) {
            if (element.classList.contains(search)) {
              return element;
            }
          }
          break;
        case '#':
          // Si el selector es #XXXX => Busca si hay un ansestro con ID=XXX
          for (
            search = selector.substr(1); element && element !==
            document; element = element.parentNode
          ) {
            if (element.id === search) {
              return element;
            }
          }
          break;
        case '[':
          // Si el selector es [XXXX] => Busca si hay un ansestro con un atributo XXX
          // Si el selector es [XXXX=YYY] => Busca si hay un ansestro con un atributo XXX=YYY
          // Si el selector es [XXXX~=YYY] => Busca si hay un ansestro con un atributo XXX que contengYYY
          search = selector.substr(1, selector.length - 2).split('=');
          var attribute = search[0].trim();
          if (search.length === 1) {
            for (; element && element !== document; element = element
              .parentNode) {
              if (element.hasAttribute(attribute)) {
                return element;
              }
            }
          } else if (search.length === 2) {
            var contains = false;
            var value = search[1].trim();
            if (attribute.substr(attribute.length - 1, 1) === '~') {
              attribute = attribute.substr(0, attribute.length - 1);
              contains = true;
            }
            for (; element && element !== document; element = element
              .parentNode) {
              if (element.hasAttribute(attribute)) {
                var attributeValue = element.getAttribute(attribute);
                if (
                  attributeValue === value ||
                  (contains === true && attributeValue.indexOf(value) >=
                    0)
                ) {
                  return element;
                }
              }
            }
          } else {
            fieloUtils.debug.log('Invalid selector:' + selector);
          }
          break;
        default:
          // Si el selector es XXXX => Busca si hay un ansestro con el Tag XXX
          for (
            ; element && element !== document;
            element = element.parentNode
          ) {
            if (element.tagName.toLowerCase() === selector) {
              return element;
            }
          }
          break;
      }
      return false;
    },

    /**
     * @method
     * @summary Devuelve null
     *
     * @param {string} type Tipo de listener original
     * @param {string} name listener a crear
     * @param {DOMelement} obj objeto a aplicar el listener
     */
    throttle: function(type, name, obj) {
      obj = obj || window;
      var running = false;
      var func = function() {
        if (running) {
          return;
        }
        running = true;
        requestAnimationFrame(function() {
          obj.dispatchEvent(new CustomEvent(name));
          running = false;
        });
      };
      obj.addEventListener(type, func);
    },

    /**
     * @summary Inicializa la configuracin<br>
     *
     * 1.- Setea el prefijo del paquete en el atributo data-package-prefix de la etiqueta html<br>
     * 2.- Setea el path del sitio en el atributo data-sitepath de la etiqueta html
     */
    initialize: function() {
      // adds compability for IE 11
      if (!('remove' in Element.prototype)) {
        Element.prototype.remove = function() {
          if (this.parentNode) {
            this.parentNode.removeChild(this);
          }
        };
      }
      this.throttle('resize', 'optimizedResize');
    }
  };
})();

window.fieloUtils = fieloUtils;
fieloUtils.initialize();
