(function() {
  'use strict';

  /**
   * @description Clase constructora para los Spinners.
   * FieloSpinner muestra / oculta el spinner
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2.0
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloSpinner = function FieloSpinner(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloSpinner = FieloSpinner;

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloSpinner.prototype.CssClasses_ = {
    /** Nombre para ocultar el elemento */
    HIDDEN: 'hidden'
  };

  /**
   * Muestra el spinner
   *
   * @public
   */
  FieloSpinner.prototype.show = function() {
    this.element_.classList.remove(this.CssClasses_.HIDDEN);
  };

  /**
   * Oculta el spinner
   *
   * @public
   */
  FieloSpinner.prototype.hide = function() {
    this.element_.classList.add(this.CssClasses_.HIDDEN);
  };

  /**
   * Inicializa el elemento
   */
  FieloSpinner.prototype.init = function() {
    if (this.element_) {
      this.element_.addEventListener('click', function(event) {
        event.preventDefault();
        event.stopPropagation();
      });

      // Disponibiliza el spinner en fieloUtils
      fieloUtils.spinner = this.element_;
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloSpinner,
    classAsString: 'FieloSpinner',
    cssClass: 'fielo-spinner',
    widget: true
  });
})();

