(function() {
  'use strict';

  /**
   * @description Clase constructora de los paginados.
   * FieloOrderBy activa el funcionamiento del paginado
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @contributor Diego Amarante <diego.amarante@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloOrderBy = function FieloOrderBy(element) {
    this.element_ = element;
    // Initialize instance.
    this.init();
  };
  window.FieloOrderBy = FieloOrderBy;

  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloOrderBy.prototype.Constant_ = {
    ORDER_VALUE: 'data-order-value',
    DATA_MENU_TYPE: 'data-order-by-type'
  };

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloOrderBy.prototype.CssClasses_ = {
    // paginador
    PAGINATOR: 'fielo-paginator',
    // RecordSet asociado al order By
    RECORD_SET: 'fielo-record-set',

    // Clase para abrir el menu
    MENU_IS_OPEN: 'fielo-order-by--is-open',

    // Items por los cuales se puede ordenar
    ORDER_BY_ITEM: 'fielo-order-by__item',

    // Tipos de orden
    ASC: 'fielo-order-by__item--is-asc',
    DESC: 'fielo-order-by__item--is-desc',

    // Item activo
    ACTIVE: 'active'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloOrderBy.prototype.setDefaults_ = function() {
    // Guardo valores por defecto
    this.defaults_ = {
      menuType: 'popup',
      orderValue: ''
    };

    // Asocia el order by con el paginador correspondiente
    this.recordSet_ = fieloUtils.getParentUntil(
      this.element_,
      '.' + this.CssClasses_.RECORD_SET
    );

    // Asocia el orderBy con el recordset correspondiente
    this.paginator_ =
      this.recordSet_.querySelector('.' + this.CssClasses_.PAGINATOR);

    // registro el tipo de menu
    this.menuType_ =
      this.element_.getAttribute(this.Constant_.DATA_MENU_TYPE) ||
      this.defaults_.menuType;

    // Registro todos los items
    this.items_ =
      this.element_.querySelectorAll('.' + this.CssClasses_.ORDER_BY_ITEM);

    // Registro el item activo
    this.itemActive_ =
      this.element_.querySelector('.' + this.CssClasses_.ACTIVE);

    // Registro el orderValue
    this.orderValue_ = '';

    // Registro si el click puede activar el item
    this.allowActive_ = false;

    // Me fijo si puedo permitir activar el item segun el tipo
    this.setAllowActiveByType_();
  };

  /**
   * Define si puedo activar el item en la primera carga  segun el tipo de menu
   *
   * @private
   */
  FieloOrderBy.prototype.setAllowActiveByType_ = function() {
    switch (this.menuType_) {
      case 'list':
        // cualquier otro menu
        this.allowActive_ = true;
        break;
      default:
    }
  };

  /**
   * Define el valor por defecto segun el tipo de menu
   *
   * @param {HTMLelement} item - Item a activar
   * @private
   */
  FieloOrderBy.prototype.checkAllowActive_ = function(item) {
    // Al abrir el menu activo los items
    this.allowActive_ = !this.itemActive_.isEqualNode(item);
  };

  /**
   * Abre / Cierra el men de ser necesario
   *
   * @private
   */
  FieloOrderBy.prototype.toggleOpen_ = function() {
    switch (this.menuType_) {
      case 'popup':
        // Abro el menu
        this.element_.classList.toggle(this.CssClasses_.MENU_IS_OPEN);
        break;
      case 'list':
      default:
        // cualquier otro menu
    }
  };

  /**
   * Activa el item clickeado
   *
   * @param {HTMLelement} item - Item a activar
   * @private
   */
  FieloOrderBy.prototype.activeItem_ = function(item) {
    // Si existe item activo Y el item clickeado no es el activo
    if (this.itemActive_) {
      // Limpo al activo viejo de todas sus clases
      this.itemActive_.classList.remove(this.CssClasses_.ACTIVE);
      this.itemActive_.classList.remove(this.CssClasses_.ASC);
      this.itemActive_.classList.remove(this.CssClasses_.DESC);
    }

    // Guardo el order value
    this.orderValue_ = item.getAttribute(this.Constant_.ORDER_VALUE) ||
          this.defaults_.orderValue;

    // Especifico el comportamiento de los selectores
    switch (this.menuType_) {
      case 'list':
        // Le tengo que guardar el order value
        // Hace toggle entre ASC y DESC
        // Guardo el valor de orden
        if (this.orderValue_.indexOf('ASC') >= 0) {
          this.orderValue_ = this.orderValue_.replace('ASC', 'DESC');
          item.classList.add(this.CssClasses_.DESC);
        } else if (this.orderValue_.indexOf('DESC') >= 0) {
          this.orderValue_ = this.orderValue_.replace('DESC', 'ASC');
          item.classList.add(this.CssClasses_.ASC);
        } else {
          this.orderValue_ += ' ASC';
          item.classList.add(this.CssClasses_.ASC);
        }

        item.setAttribute(this.Constant_.ORDER_VALUE, this.orderValue_);
        break;
      case 'popup':
      default:
        // Ya viene el order value. Actualizo su valor
    }

    // Actualizo el item clickeado como activo
    this.itemActive_ = item;

    // Lo activo
    this.itemActive_.classList.add(this.CssClasses_.ACTIVE);
  };

  /**
   * Inicializa cada item
   *@param {event} click - Evento de tipo click
   * @private
   */
  FieloOrderBy.prototype.itemClickListener_ = function(click) {
    // Me fijo si puedo activar este item
    this.checkAllowActive_(click.target);

    // Me fijo si puedo permitir activar el item segun el tipo
    this.setAllowActiveByType_();

    // Si allowActive_ es verdadero y no se trata del mismo link
    // entonces permito que al clickear un item pase a estado activo
    if (this.allowActive_) {
      // activo el item clickeado
      this.activeItem_(click.target);

      // Seteo el paginador
      this.paginator_.FieloPaginator.setOrderBy(this.orderValue_);
      this.paginator_.FieloPaginator.setPage();
      this.paginator_.FieloPaginator.getRecords();
    }

    // Si tengo men lo abro o cierro
    this.toggleOpen_();
  };

  /**
   * Inicializa cada item
   *
   * @private
   */
  FieloOrderBy.prototype.setupItems_ = function() {
    [].forEach.call(this.items_, function(item) {
      item.addEventListener('click', this.itemClickListener_.bind(this));
    }, this);
  };

  // Public methods

  /**
   * Inicializa el elemento
   */
  FieloOrderBy.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.setupItems_();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloOrderBy,
    classAsString: 'FieloOrderBy',
    cssClass: 'fielo-order-by'
  });
})();
