(function() {
  'use strict';

  /**
   * @description Clase constructora de los Mensajes.
   * FieloMessages activa las funcionalidades para los mensajes de alerta
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @author Sebastian Claros <sebastian.claros@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloMessage = function FieloMessage(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloMessage = FieloMessage;

  // Properties

  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloMessage.prototype.Constant_ = {
    TYPE_PREFIX: 'TYPE_',
    TYPE_DEFAULT: 'INFO',
    REDIRECT_WAIT_DEFAULT: 4
  };

  /**
   * Guarda strings para nombres de clases definidas por este componente que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloMessage.prototype.CssClasses_ = {
    /** Elemento que contiene los mensajes de error */
    MESSAGE: 'fielo-message__text',
    /** Boton que contiene el contador */
    COUNTER: 'fielo-message__counter',
    /** Nombre para ocultar el elemento */
    HIDDEN: 'hidden',
    /** Nombre para mostrar animando */
    FADE_IN: 'fielo-message--fade-in',
    /** Nombre para ocultar animando */
    FADE_OUT: 'fielo-message--fade-out',
    /** Tipos de errores */
    TYPE_CONFIRM: 'fielo-message--is-confirm',
    TYPE_INFO: 'fielo-message--is-info',
    TYPE_ERROR: 'fielo-message--is-error',
    TYPE_FATAL: 'fielo-message--is-fatal',
    TYPE_WARNING: 'fielo-message--is-warning'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloMessage.prototype.setDefaults_ = function() {
    this.registerTypes_();

    this.redirect_ = {};
    this.redirect_.active = false;
    // ['' | '#' | string ]
    this.redirect_.url = '';
    // redirect disabled
    this.redirect_.wait = 0;

    this.isAnimated_ = true;
  };

  /**
   * Registra los listeners de las animaciones
   *
   * @private
   */
  FieloMessage.prototype.setAnimationListeners_ = function() {
    this.element_.addEventListener(
      'animationstart', this.animationStarted.bind(this)
    );
    this.element_.addEventListener(
      'animationiteration', this.animationNewIteration
    .bind(this));
    this.element_.addEventListener(
      'animationend', this.animationEnded.bind(this)
    );
  };

  /**
   * Registra los tipos de mensajes
   *
   * @private
   */
  FieloMessage.prototype.registerTypes_ = function() {
    this.selectedType_ = '';
    this.types_ = [];
    for (var property in this.CssClasses_) {
      if (property.indexOf(this.Constant_.TYPE_PREFIX) === 0) {
        this.types_.push(property);
      }
    }
  };

  /**
   * Limpia el tipo del mensaje
   *
   * @private
   */
  FieloMessage.prototype.clearType_ = function() {
    // El remove esta separado por clase debido a compatibilidad con IE 11
    this.types_.forEach(function(typeValue) {
      this.element_.classList.remove(this.CssClasses_[typeValue]);
    }, this);
    this.selectedType_ = '';
  };

  /**
   * Crea el elemento contador
   *
   * @private
   */

  FieloMessage.prototype.createCounterButton_ = function() {
    var counter = document.createElement('span');
    counter.classList.add(this.CssClasses_.COUNTER);
    this.element_.appendChild(counter);

    this.counter_ = this.element_.querySelector('.' + this.CssClasses_.COUNTER);
  };

  /**
   * Elimina el elemento contador
   *
   * @private
   */

  FieloMessage.prototype.deleteCounterButton_ = function() {
    this.counter_.remove();
  };

  /**
   * Corre el contador
   * @param {number} [timeLeft] Tiempo faltante para terminar
   * @private
   */
  FieloMessage.prototype.runTimer_ = function() {
    setTimeout(function() {
      if (this.redirect_.wait >= 0) {
        // Muestro el  en el html
        this.counter_.innerHTML = this.redirect_.wait;
        this.redirect_.wait--;
        this.runTimer_();
      } else {
        this.hide();
        this.doRedirect();
      }
    }
    .bind(this), 1000);
  };

  /**
   * Agrega el listener al click
   * @private
   */
  FieloMessage.prototype.clickEventListener_ = function() {
    this.hide();
  };

  // Public methods

  /**
   * Setea el mensaje para animar
   * @param {bool} boolean - Define se anima o no el mensaje
   * @public
   */
  FieloMessage.prototype.animate = function(boolean) {
    this.isAnimated_ = boolean;
  };

  /**
   * Agrega mensaje de error
   *
   * @param {array | string} messages - Lista de mensajes a agregar
   * @public
   */
  FieloMessage.prototype.addMessages = function(messages) {
    if (typeof messages === 'string') {
      messages = messages.split(messages + 'toArray');
    }

    for (var l = messages.length, i = 0; i < l; i++) {
      var p = document.createElement('p');
      p.classList.add(this.CssClasses_.MESSAGE);
      p.innerText = messages[i];
      this.element_.appendChild(p);
    }
  };

  /**
   * Guarda el tipo del mensaje
   *
   * @param {string} type - Tipo de alerta
   * @public
   */
  FieloMessage.prototype.setType = function(type) {
    this.clearType_();
    type = type || '';
    this.selectedType_ = type.toUpperCase();

    if (this.types_.indexOf(
      this.Constant_.TYPE_PREFIX + this.selectedType_
      ) !== -1 && this.selectedType_ !== ''
    ) {
      // type existente seleccionada por usuario
      this.element_.classList.add(
      this.CssClasses_[this.Constant_.TYPE_PREFIX + this.selectedType_]
      );
    } else {
      // type por default
      this.selectedType_ = this.Constant_.TYPE_DEFAULT;
      this.element_.classList.add(
      this.Constant_.TYPE_PREFIX + this.Constant_.TYPE_DEFAULT.toUpperCase()
      );
    }
  };

  /**
   * Configura la redireccin
   *
   * @param {string} url - url a redireccionar
   * @param {number} seconds - segundos a esperar para ser redirecionado
   * @public
   */
  FieloMessage.prototype.setRedirect = function(url, seconds) {
    this.redirect_.url = url || this.redirect_.url;
    this.redirect_.wait = seconds || this.Constant_.REDIRECT_WAIT_DEFAULT;

    // Condiciones para evitar redirect
    if (
      this.redirect_.url !== '' &&
      this.redirect_.wait > 0
    ) {
      this.redirect_.active = true;
    }
  };

  /**
   * Redirige el sitio a otra url
   *
   * @public
   */
  FieloMessage.prototype.doRedirect = function() {
    if (this.redirect_.active) {
      window.location.href = this.redirect_.url;
    }
  };

  /**
   * Borra los mensajes y el tipo de mensaje
   *
   * @public
   */
  FieloMessage.prototype.clear = function() {
    this.element_.innerHTML = '';
    this.clearType_();
  };

  /**
   * Muestra el mensaje
   *
   * @public
   */
  FieloMessage.prototype.show = function() {
    // Verifica el type
    if (this.selectedType_ === '') {
      this.setType(this.Constant_.TYPE_DEFAULT);
    }

    // Corro el contador
    if (this.redirect_.active) {
      this.createCounterButton_();
      this.runTimer_();
    }

    // Mostrar el mensaje
    if (this.isAnimated_) {
      this.element_.classList.add(this.CssClasses_.FADE_IN);
    }
    this.element_.classList.remove(this.CssClasses_.FADE_OUT);
    this.element_.classList.remove(this.CssClasses_.HIDDEN);
  };

  /**
   * Oculta el mensaje
   *
   * @public
   */
  FieloMessage.prototype.hide = function() {
    // Borro el boton
    if (this.redirect_.active) {
      this.deleteCounterButton_();
    }

    // Ocultar el mensaje
    if (this.isAnimated_) {
      this.element_.classList.remove(this.CssClasses_.FADE_IN);
      this.element_.classList.add(this.CssClasses_.FADE_OUT);
    } else {
      this.element_.classList.add(this.CssClasses_.HIDDEN);
    }
  };

  // Public methods

  /**
   * Mtodo que se ejecuta cuando arranca la animacin
   *
   * @public
   */
  FieloMessage.prototype.animationStarted = function() {
  };

  /**
   * Mtodo que se ejecuta cuando arranca una iteracin
   *
   * @public
   */
  FieloMessage.prototype.animationNewIteration = function() {
  };

  /**
   * Mtodo que se ejecuta cuando arranca termina una animacin
   *
   * @param {event} e - Evento de animacin
   * @public
   */
  FieloMessage.prototype.animationEnded = function(e) {
    switch (e.animationName) {
      case 'message_fadeout':
        this.element_.classList.add(this.CssClasses_.HIDDEN);
        break;
      default:
        break;
    }
  };

  /**
   * Inicializa el elemento
   */
  FieloMessage.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.setAnimationListeners_();
      this.element_.addEventListener(
        'click',
        this.clickEventListener_.bind(this)
      );
      fieloUtils.message = this.element_;
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloMessage,
    classAsString: 'FieloMessage',
    cssClass: 'fielo-message',
    widget: true
  });
})();
