(function() {
  'use strict';

  /**
   * @description Clase constructora de los paginados.
   * FieloPaginator activa el funcionamiento del paginado
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @contributor Diego Amarante <diego.amarante@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloPaginator = function FieloPaginator(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloPaginator = FieloPaginator;

  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloPaginator.prototype.Constant_ = {
    COMPONENT_ID: 'data-component-id',
    BINDING: 'data-binding',
    QUANTITY_PER_PAGE: 'data-quantity-per-page',
    PAGE: 'data-page',
    FILTERS: 'data-filters',
    ORDER_BY: 'data-order-by'
  };

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloPaginator.prototype.CssClasses_ = {
    // Enlaces del paginador
    LINK_PREVIOUS: 'fielo-link__previous',
    LINK_NEXT: 'fielo-link__next',

    // RecordSet asociado al paginador
    RECORD_SET: 'fielo-record-set',
    // Clase para desabilitar un elemento
    DISABLED: 'disabled'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloPaginator.prototype.setDefaults_ = function() {
    // Registro las dependencias

    // Asocia el paginador con el recordset correspondiente
    this.recordSet = fieloUtils.getParentUntil(
      this.element_,
      '.' + this.CssClasses_.RECORD_SET
    );

    // Registro el NS
    this.ns_ = fieloUtils.site.FieloSite.ns;
    if (this.ns_ !== '') {
      this.ns_ += '.';
    }

    // Regisro el controlador
    this.controller_ = this.ns_ + 'PageController.getRecords';

    // Registo lo particular

    // registro el id del componente
    this.componentId_ = this.element_.getAttribute(this.Constant_.COMPONENT_ID);

    // registro el data binding
    this.dataBinding_ = this.element_.getAttribute(this.Constant_.BINDING);

    // registro la cantidad de items que puede haber en una pagina
    this.recordsPerPage_ =
      Number(this.element_.getAttribute(this.Constant_.QUANTITY_PER_PAGE));

    // registro la cantidad de items que hay actualmente en la pagina
    this.recordsInPage_ = this.recordSet.FieloRecordSet.records_.inPage;

    // registro la pagina actual
    this.page_ = Number(this.element_.getAttribute(this.Constant_.PAGE)) || 1;

    // registro los filtros
    this.filters_ =
      JSON.parse(this.element_.getAttribute(this.Constant_.FILTERS)) || {};

    // registro el orderBy
    this.orderBy_ = this.element_.getAttribute(this.Constant_.ORDER_BY) || '';

    // Registro los botones
    this.links_ = {
      previous:
        this.element_.querySelector('.' + this.CssClasses_.LINK_PREVIOUS),
      next:
        this.element_.querySelector('.' + this.CssClasses_.LINK_NEXT)
    };
  };

  /**
   * Habilita o deshabilita los links
   *
   * @private
   */
  FieloPaginator.prototype.setLinksStatus_ = function() {
    // link next
    if (this.recordsInPage_ < this.recordsPerPage_) {
      this.links_.next.classList.add(this.CssClasses_.DISABLED);
    } else {
      this.links_.next.classList.remove(this.CssClasses_.DISABLED);
    }

    // link previous
    if (this.page_ === 1) {
      this.links_.previous.classList.add(this.CssClasses_.DISABLED);
    } else {
      this.links_.previous.classList.remove(this.CssClasses_.DISABLED);
    }
  };

  /**
   * Pide la pgina anterior
   *
   * @private
   */
  FieloPaginator.prototype.getPreviousPage_ = function() {
    if (!this.links_.previous.classList.contains(this.CssClasses_.DISABLED)) {
      this.setPage(this.page_ - 1);
      this.getRecords();
    }
  };

  /**
   * Pide la pgina siguiente
   *
   * @private
   */
  FieloPaginator.prototype.getNextPage_ = function() {
    if (!this.links_.next.classList.contains(this.CssClasses_.DISABLED)) {
      this.setPage(this.page_ + 1);
      this.getRecords();
    }
  };

  /**
   * Registra los listeners de los links
   *
   * @private
   */
  FieloPaginator.prototype.setLinksListeners_ = function() {
    this.links_.previous
      .addEventListener('click', this.getPreviousPage_.bind(this));

    this.links_.next
      .addEventListener('click', this.getNextPage_.bind(this));
  };

  /**
   * Lanza la configuracin inicial de los links
   *
   * @private
   */
  FieloPaginator.prototype.setUpLinks_ = function() {
    this.setLinksStatus_();
    this.setLinksListeners_();
  };

  // Public methods

  /**
   * Setea la pgina
   *
   * @param {number} pageNumber - Nmero de pgina a guardar
   * @private
   */
  FieloPaginator.prototype.setPage = function(pageNumber) {
    // actualizo la variable interna
    this.page_ = pageNumber || 1;

    // actualizo el atributo html
    this.element_.setAttribute(this.Constant_.PAGE, pageNumber);
  };

  /**
   * Devuelve los filtros
   *
   * @return {object} Objeto con los filtros aplicados
   * @public
   */
  FieloPaginator.prototype.getFilters = function() {
    return this.filters_;
  };

  /**
   * Setea los filtros
   *
   * @param {object} newFilters - Filtros a guardar
   * @public
   */
  FieloPaginator.prototype.setFilters = function(newFilters) {
    // Actualizo los filtros pasados
    for (var filter in newFilters) {
      if ({}.hasOwnProperty.call(newFilters, filter)) {
        this.filters_[filter] = newFilters[filter];
      }
    }

    // actualizo el atributo html
    this.element_.setAttribute(
      this.Constant_.FILTERS,
      JSON.stringify(this.filters)
    );
  };

  /**
   * Setea el orderBy
   *
   * @param {string} order - Orden que se aplica al paginado
   * @private
   */
  FieloPaginator.prototype.setOrderBy = function(order) {
    // actualizo la variable interna
    this.orderBy_ = order;

    // actualizo el atributo html
    this.element_.setAttribute(this.Constant_.ORDER_BY, order);
  };

  /**
   * Mtodo que resuelve el request del paginador
   *
   * @param {function} callback - funcin con mtodo a disparar cuando se termina la peticion
   * @param {array} records - Array con registros en formato json
   * @private
   */
  FieloPaginator.prototype.resolveRequest_ = function(callback, records) {
    // Ya est todo listo. Oculto el spinner
    fieloUtils.spinner.FieloSpinner.hide();
    setTimeout(function() {
      // Si existen records
      if (records) {
        // Actualizo la cantidad de tegistros en la pagina
        this.recordsInPage_ = records.length || 0;
        // Actualiza el estado de los links
        this.setLinksStatus_();
        this.recordSet.FieloRecordSet.render(records, callback);
      } else {
        throw new Error('todo: no hay registros a mostrar', 'paginator.js');
      }
    }.bind(this), 0);
  };

  /**
   * Obtiene los records pedidos
   * @param {function} callback - mtodo a disparar cuando se termina la peticion
   *
   */
  FieloPaginator.prototype.getRecords = function(callback) {
    // muestro el spinner antes de la peticin
    fieloUtils.spinner.FieloSpinner.show();
    try {
      // Ejecuto a segundo plano para no trabar el navegador
      setTimeout(function() {
        Visualforce.remoting.Manager.invokeAction(
          this.controller_,
          this.componentId_,
          this.page_,
          this.orderBy_,
          JSON.stringify(this.filters_),
          this.resolveRequest_.bind(this, callback),
          {escape: false}
        );
      }.bind(this), 0);
    } catch (e) {
      // Ya est todo listo. Oculto el spinner
      fieloUtils.spinner.FieloSpinner.hide();
      fieloUtils.debug.log(e);
    }
  };

  /**
   * Inicializa el elemento
   */
  FieloPaginator.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.setUpLinks_();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloPaginator,
    classAsString: 'FieloPaginator',
    cssClass: 'fielo-paginator'
  });
})();
