(function() {
  'use strict';

  /**
   * @description Clase constructora del modal.
   * FieloModal activa las funcionalidades para el modal
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloModal = function FieloModal(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloModal = FieloModal;

  // Properties

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   *
   * Esto nos permite cambiarlos solo en un lugar
   *
   * @enum {string}
   * @private
   */
  FieloModal.prototype.CssClasses_ = {
    // Nombre para ocultar el elemento
    HIDDEN: 'hidden',
    // Nombre del elemento HTML para cerrar
    CLOSE: 'fielo-modal__close',
    // Elemento HTML para el cuerpo del modal
    BODY: 'fielo-modal__body',
    // Elemento HTML para el contenido del modal
    BODY_CONTAINER: 'fielo-modal__body-container',
    // Nombre para mostrar animando
    FADE_IN: 'fielo-modal--fade-in',
    // Nombre para ocultar animando
    FADE_OUT: 'fielo-modal--fade-out'
  };

/**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloModal.prototype.Constant_ = {
    CALLED_BY: 'data-called-by'
  };
  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloModal.prototype.setDefaults_ = function() {
    this.isAnimated_ = true;
    this.closeBtn_ = this.element_.querySelector('.' + this.CssClasses_.CLOSE);
    this.body_ = this.element_.querySelector('.' + this.CssClasses_.BODY);
    this.bodyContainer_ =
      this.body_.querySelector('.' + this.CssClasses_.BODY_CONTAINER);
  };

  /**
   * Evento para cerrar el modal
   *
   * @private
   */
  FieloModal.prototype.closeEventListener_ = function() {
    event.stopPropagation();
    this.hide();
  };

  /**
   * Resgistra los elementos que cierran el modal
   *
   * @private
   */
  FieloModal.prototype.registerCloseElements_ = function() {
    this.element_.addEventListener(
      'click',
      this.closeEventListener_.bind(this)
    );
    this.closeBtn_.addEventListener(
      'click',
      this.closeEventListener_.bind(this)
    );
  };

  /**
   * Evento para evitar cerrar el modal
   *
   * @private
   */
  FieloModal.prototype.noClosingEventListener_ = function() {
    event.stopPropagation();
  };

  /**
   * Resgistra los elementos que no pueden cerrar el modal
   *
   * @private
   */
  FieloModal.prototype.registerNoClosingElements_ = function() {
    this.body_.addEventListener(
      'click',
      this.noClosingEventListener_.bind(this)
    );
  };

  /**
   * Registra los listeners de las animaciones
   *
   * @private
   */
  FieloModal.prototype.setAnimationListeners_ = function() {
    this.element_.addEventListener(
      'animationstart', this.animationStarted.bind(this)
    );
    this.element_.addEventListener(
      'animationiteration', this.animationNewIteration
    .bind(this));
    this.element_.addEventListener(
      'animationend', this.animationEnded.bind(this)
    );
  };

  // Public methods

  /**
   * Mtodo que se ejecuta cuando arranca la animacin
   *
   * @public
   */
  FieloModal.prototype.animationStarted = function() {
  };

  /**
   * Mtodo que se ejecuta cuando arranca una iteracin
   *
   * @public
   */
  FieloModal.prototype.animationNewIteration = function() {
  };

  /**
   * Mtodo que se ejecuta cuando arranca termina una animacin
   *
   * @param {event} e - Evento de animacin
   * @public
   */
  FieloModal.prototype.animationEnded = function(e) {
    switch (e.animationName) {
      case 'modal_fadein':
        this.element_.classList.remove(this.CssClasses_.FADE_IN);
        break;
      case 'modal_fadeout':
        this.element_.classList.add(this.CssClasses_.HIDDEN);
        this.element_.classList.remove(this.CssClasses_.FADE_OUT);
        break;
      default:
        break;
    }
  };

  /**
   * Setea si el modal se anima o no
   * @param {bool} boolean - Define se anima o no el mensaje
   * @public
   */
  FieloModal.prototype.animate = function(boolean) {
    this.isAnimated_ = boolean;
  };

  /**
   * Abre el modal
   * @public
   */
  FieloModal.prototype.show = function() {
    if (this.isAnimated_) {
      this.element_.classList.add(this.CssClasses_.FADE_IN);
    }
    this.element_.classList.remove(this.CssClasses_.HIDDEN);
  };

  /**
   * Oculta el modal
   * @public
   */
  FieloModal.prototype.hide = function() {
    // Ocultar el mensaje
    if (this.isAnimated_) {
      this.element_.classList.add(this.CssClasses_.FADE_OUT);
    } else {
      this.element_.classList.add(this.CssClasses_.HIDDEN);
    }
  };

  /**
   * Setea el label del elemento Close
   *
   * @param {HTMLelement | string} label - Label del boton close
   * @public
   */
  FieloModal.prototype.setCloseLabel = function(label) {
    label = label || fieloUtils.site.FieloSite.getLabel('close');
    // Reset label
    this.closeBtn_.innerHTML = '';
    // Set label
    if (typeof label === 'string') {
      this.closeBtn_.innerHTML = label;
    } else {
      this.closeBtn_.appendChild(label);
    }
  };

  /**
   * Setea el contenido del modal
   *
   * @param {HTMLelement | string} content - Label del boton close
   * @public
   */
  FieloModal.prototype.setContent = function(content) {
    content = content || '';
    // Reset content
    this.bodyContainer_.innerHTML = '';

    // Set content
    if (typeof content === 'string') {
      this.bodyContainer_.innerHTML = content;
    } else {
      this.bodyContainer_.appendChild(content);
    }
  };

/**
   * Setea la propiedad data-called-by
   *
   * @param {HTMLelement | string} calledBy - referencia de quien lo llamo
   * @public
   */
  FieloModal.prototype.setCalledBy = function(calledBy) {
    calledBy = calledBy || '';
    this.element_.setAttribute(this.Constant_.CALLED_BY, calledBy);
  };

  /**
   * Inicializa el elemento
   */
  FieloModal.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.registerCloseElements_();
      this.registerNoClosingElements_();
      this.setCloseLabel();
      this.setAnimationListeners_();

    // Disponibiliza el modal en fieloUtils
      fieloUtils.modal = this.element_;
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloModal,
    classAsString: 'FieloModal',
    cssClass: 'fielo-modal',
    widget: true
  });
})();
