(function() {
  'use strict';

  var FieloOldLoaderTrigger = function FieloOldLoaderTrigger() {
    oldLoader.dispatchEvent({type: 'DOMContentLoaded', target: document});
  };
  window.FieloOldLoaderTrigger = FieloOldLoaderTrigger;

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloOldLoaderTrigger,
    classAsString: 'FieloOldLoaderTrigger',
    cssClass: 'fielo-js-old-loader-trigger',
    widget: false
  });
})();
