(function() {
  'use strict';

  /**
   * @description Clase constructora de los campos output.
   * FieloOutput activa la conversin de los output por sus nuevos valores
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @author Sebastian Claros <sebastian.claros@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloOutput = function FieloOutput(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloOutput = FieloOutput;

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloOutput.prototype.CssClasses_ = {
    // Nombre del elemento time
    TIME: 'fielo-time'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloOutput.prototype.setDefaults_ = function() {
    this.config_ = {
      locale: 'en-US'
    };
    this.type_ = this.element_.getAttribute('data-type');
    this.value_ = this.element_.getAttribute('data-value');
  };

  /**
   * Convierte el valor viejo por el nuevo
   *
   * @private
   */
  FieloOutput.prototype.convertValue_ = function() {
    var newValue;
    switch (this.type_) {
      case 'date':
      case 'datetime':
        if (this.value_ !== '') {
          newValue = new Date();
          newValue.setTime(isNaN(this.value_) ?
            Date.parse(this.value_) : this.value_);
          this.value_ = newValue.toLocaleDateString(this.config_.locale);
          if (this.type_ === 'datetime') {
            this.value_ += ' ' +
              newValue.toLocaleTimeString(this.config_.locale);
          }
        }
        break;
      case 'integer':
        if (this.value_ !== '') {
          newValue = Number.parseInt(this.value_, 10);
          this.value_ = newValue.toLocaleString(this.config_.locale);
        }
        break;
      case 'currency':
        if (this.value_ !== '') {
          newValue = parseFloat(this.value_);
          var currencyIsoCode = this.element_
            .getAttribute('data-currencyIsoCode');
          this.value_ = newValue.toLocaleString(this.config_.locale,
            {style: 'currency', currency: currencyIsoCode});
        }
        break;
      case 'percent':
        if (this.value_ !== '') {
          newValue = parseFloat(this.value_);
          this.value_ = newValue.toLocaleString(
            this.config_.locale, {style: 'percent'}
           );
        }
        break;
      case 'double': case 'decimal': case 'number':
        if (this.value_ !== '') {
          newValue = parseFloat(this.value_);
          this.value_ = newValue.toLocaleString(this.config_.locale);
        }
        break;
      default:
    }

    if (newValue) {
      var node;
      switch (this.type_) {
        case 'date':
        case 'datetime':
          node = this.element_.querySelector('.' + this.CssClasses_.TIME);
          if (node) {
            node.setAttribute('datetime', this.value_);
            node.innerText = this.value_;
          } else {
            this.element_.innerText = this.value_;
          }
          break;
        case 'reference':
          node = this.element_.getElementsByTagName('a')[0];
          if (node) {
            this.element_.innerText = node.innerText;
          }
          break;
        default:
          this.element_.innerText = this.value_;
      }
    }
  };

  // Public methods

  /**
   * Inicializa el elemento
   */
  FieloOutput.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.convertValue_();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloOutput,
    classAsString: 'FieloOutput',
    cssClass: 'fielo-output',
    widget: false
  });
})();
