(function() {
  'use strict';

  /**
   * @description Clase constructora del navigation.
   * FieloMenu activa las funcionalidades para el navigation
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 1
   * @author Nicolas Soberon <nicolas.soberon@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloMenu = function FieloMenu(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloMenu = FieloMenu;

  // Properties
  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloMenu.prototype.Constant_ = {
    APP_MENU: 'appMenu'
  };
    /**
   * Guarda strings para nombres de clases definidas por este componente que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloMenu.prototype.CssClasses_ = {
    /** Elemento que contiene una lista de items (ul) */
    LIST: 'fielo-list',
    LIST_ITEM: 'fielo-list__item',
    HOVER: 'fielo-js-hover'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloMenu.prototype.setDefaults_ = function() {
    this.isAppMenu_();

    // bool
    this.menuChilds_ = this.element_.querySelectorAll(
      '.' + this.CssClasses_.LIST + ' .' + this.CssClasses_.LIST
    );
    this.parentsActive_ = [];
  };

  /**
   * Mtodo que verifica si estoy en un elemento perteneciente al padre
   *
   * @private
   */
  FieloMenu.prototype.checkHover_ = function() {
    setTimeout(function() {
      var elementMouseIsOver = document.querySelectorAll(':hover');
      elementMouseIsOver = elementMouseIsOver[elementMouseIsOver.length - 1];
      var actualParent = fieloUtils.getParentUntil(
        elementMouseIsOver,
        '.' + this.CssClasses_.HOVER
      );

      if (actualParent) {
        // 1.2 Salgo de un padre y entro a un hijo, no hagp nada
        // 1. Salgo por niveles superiores y hago lo siguiente
        if (this.parentsActive_.length > 1) {
          this.removeHover_();
        }
      } else {
        // 2. Salgo por niveles inferiores (no tengo parent)
        this.removeHover_();
      }
    }.bind(this), 100);
  };

  /**
   * Mtodo que quita los hover
   *
   * @private
   */
  FieloMenu.prototype.removeHover_ = function() {
    if (this.parentsActive_.length > 0) {
      this.parentsActive_[0].classList.remove(this.CssClasses_.HOVER);
      this.parentsActive_.shift();
    }
  };

  /**
   * Mtodo que controla el toggle hover del menu
   *
   * @private
   */
  FieloMenu.prototype.upgradeMenus_ = function() {
    if (this.menuChilds_.length > 0) {
      // Busco los menu padres
      this.menuParents_ = [];
      [].forEach.call(this.menuChilds_, function(child) {
        this.menuParents_.push(
          fieloUtils.getParentUntil(child, '.' + this.CssClasses_.LIST_ITEM)
        );
      }, this);

      // activo el hover
      this.menuParents_.forEach(function(parent) {
        parent.addEventListener('mouseenter', function() {
          if (this.parentsActive_.indexOf(parent) === -1) {
            parent.classList.add(this.CssClasses_.HOVER);
            this.parentsActive_.push(parent);
          }
        }.bind(this));
        parent.addEventListener('mouseleave', function() {
          this.checkHover_();
        }.bind(this));
      }, this);
    }
  };

  /**
   * Registra si el menu esta disponible en el appMenu
   *
   * @private
   */
  FieloMenu.prototype.isAppMenu_ = function() {
    this.appMenuEnabled_ =
      this.element_.dataset[this.Constant_.APP_MENU] || false;
  };

  /**
   * Agrega el menu a appMenu
   *
   * @private
   */
  FieloMenu.prototype.addToAppMenu_ = function() {
    if (this.appMenuEnabled_) {
      var menu = this.element_.cloneNode(true);
      menu.dataset[this.Constant_.APP_MENU] = false;
      fieloUtils.appMenu.FieloAppMenu.addToMenu(menu);
    }
  };

  // Public methods

  /**
   * Inicializa el elemento
   */
  FieloMenu.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.upgradeMenus_();
      this.addToAppMenu_();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloMenu,
    classAsString: 'FieloMenu',
    cssClass: 'fielo-menu',
    widget: true
  });
})();
