(function() {
  'use strict';

  /**
   * @description Clase constructora de los recordset.
   * FieloRecordSet hace el upgrade del recordset
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @contributor Diego Amarante <diego.amarante@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloRecordSet = function FieloRecordSet(element) {
    this.element_ = element;

    //  Initialize instance.
    this.init();
  };
  window.FieloRecordSet = FieloRecordSet;

  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloRecordSet.prototype.Constant_ = {
    // Atributos data
    DATA_VALUE: 'data-value',
    DATA_LAYOUT: 'data-layout',
    DATA_UPGRADED: 'data-upgraded',
    DATA_ATTRIBUTE_REPLACE: 'data-attribute-replace',
    DATA_DETAIL_PARAMETER: 'data-detail-parameter',
    DATA_OBJECT_PROPERTY: 'data-object-property',
    // Mdulos
    MODULE_OUTPUT: 'FieloOutput',
    MODULE_RECORD: 'FieloRecord',
    MODULE_RECORD_SET: 'FieloRecordSet'
  };

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloRecordSet.prototype.CssClasses_ = {
    //  Elementos del recordset
    DATA: 'fielo-record-set__data',
    DATA_DISABLED: 'fielo-record-set__data--disabled',
    TEMPLATE: 'fielo-record-set__template',
    // Elementos del record
    RECORD: 'fielo-record',
    FIELD_IS: 'fielo-field--is-',
    FIELD_IS_DETAIL: 'fielo-field--is-detail',
    OUTPUT: 'fielo-output',
    LINK_TO_DETAIL: 'fielo-link__to-detail',
    FIELD_VALUE: 'fielo-field__value'
  };

  //  Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloRecordSet.prototype.setDefaults_ = function() {
    // Registro el NS
    this.ns_ = fieloUtils.site.FieloSite.ns;

    //  Guardo el layout
    this.layout_ = this.element_.getAttribute(this.Constant_.DATA_LAYOUT);

    //  guardo el contenedor de todos los registros
    this.data_ = this.element_.querySelector('.' + this.CssClasses_.DATA);

    //  Inicializo el objeto records
    this.setRecordsObject_();

    // Si tengo records continuo con el set Defaults
    if (this.records_.inTemplate > 0) {
      this.setRecordSetTemplate_();
      this.setRecordTemplate_();

      this.onlyOneTemplate_ =
        this.recordSetTemplate_.isEqualNode(this.recordTemplate_);
    }
  };

  /**
   * Setea el template del recordSet
   *
   * @private
   */
  FieloRecordSet.prototype.setRecordsObject_ = function() {
    this.records_ = {};
    //  Registra la cantidad de records en un template
    var tempElement =
      this.data_.querySelector('.' + this.CssClasses_.TEMPLATE);
    if (tempElement) {
      this.records_.inTemplate = tempElement.children.length || 0;
    }

    //  Guardo cantidad de records en la pgina
    this.records_.inPage =
      this.data_.querySelectorAll('.' + this.CssClasses_.RECORD).length || 0;

    // Interfaz de propiedades
    this.records_.fields = [];

    tempElement = null;
  };

  /**
   * Setea el template del recordSet
   *
   * @private
   */
  FieloRecordSet.prototype.setRecordSetTemplate_ = function() {
    //  guardo un template
    this.recordSetTemplate_ =
      this.data_.querySelector('.' + this.CssClasses_.TEMPLATE)
      .cloneNode(true);

    //  Borro el atributo del componentHandler
    this.recordSetTemplate_.removeAttribute(this.Constant_.DATA_UPGRADED);
  };

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloRecordSet.prototype.setRecordTemplate_ = function() {
    // Seteo el record Template
    this.recordTemplate_ =
      this.data_.querySelector('.' + this.CssClasses_.RECORD).cloneNode(true);

    //  Borro el atributo del componentHandler
    this.recordTemplate_.removeAttribute(this.Constant_.DATA_UPGRADED);
  };

  /**
   * Borra los hijos de data
   *
   * @private
   */
  FieloRecordSet.prototype.cleanDataChilds_ = function() {
    // limpio los field de tipo detail y los record de tipo template
    [].forEach.call(this.data_.querySelectorAll(
      '.' + this.CssClasses_.FIELD_IS_DETAIL + ', ' +
      '.' + this.CssClasses_.TEMPLATE
      ),
      function(element) {
        element.remove();
      }
    );
  };

  /**
   * Setea los valores de las propiedades
   *
   * @param {object} record - objeto con los valores a remplazar
   * @param {HTMLelement} newRecord - Record nuevo al cual se le actualizan los campos
   * @private
   */
  FieloRecordSet.prototype.setFieldsValues_ = function(record, newRecord) {
    // Itero cada propiedad
    this.records_.fields.forEach(function(fieldName) {
      // traigo los campos que coinciden con esa propiedad
      var field = newRecord.querySelector(
        '.' + this.CssClasses_.FIELD_IS + fieldName
      );

      // si la propiedad es igual al nombre del campo
      if (field) {
        // buscar dentro de linkToDetail la clase fielo-output
        var outputField = field.querySelector(
          '.' + this.CssClasses_.OUTPUT
        );

        if (outputField) {
          // Remuevo el atributo data upgrade
          outputField.removeAttribute(this.Constant_.DATA_UPGRADED);

          // actualizo el valor mostrado al usuario
          outputField.innerHTML = record[fieldName];

          if (outputField.hasAttribute(this.Constant_.DATA_VALUE)) {
            // Actualizo el valor de data-value
            outputField.setAttribute(
              this.Constant_.DATA_VALUE, record[fieldName]
            );
          }

          // Termin de configurarlo, ahora lo inicializo
          componentHandler.upgradeElement(
            outputField,
            this.Constant_.MODULE_OUTPUT
          );
        }
      }
    }, this);
  };

  /**
   * Setea los valores de los links al detail
   *
   * @param {object} record - objeto con los valores a remplazar
   * @param {HTMLelement} newRecord - Record nuevo al cual se le actualizan los campos
   * @private
   */
  FieloRecordSet.prototype.setLinksToDetailValues_ = function(record,
    newRecord) {
    // Iterates per link to details to replace href
    [].forEach.call(
      newRecord.querySelectorAll('.' + this.CssClasses_.LINK_TO_DETAIL),
      function(link) {
        // Update Href
        var href = link.getAttribute('href');

        // Get the parameters from the href
        var params = href.split('?');
        var base = params[0];
        params = params[1];
        params = params.split('&');
        var detailParam =
          link.getAttribute(this.Constant_.DATA_DETAIL_PARAMETER);
        params.forEach(function(param, index) {
          if (param.indexOf(detailParam) >= 0) {
            params[index] = detailParam + '=' +
              record.Id;
          }
        });
        href = base + '?' + (params.join('&'));
        link.setAttribute('href', href);
      },
    this);
  };

  /**
   * Setea los valores de los subcomponentes
   *
   * @param {object} record - objeto con los valores a remplazar
   * @param {HTMLelement} newRecord - Record nuevo al cual se le actualizan los campos
   * @private
   */
  FieloRecordSet.prototype.setSubcomponentsValues_ = function(
    record,
    newRecord
  ) {
    // Iterate per subcomponents
    [].forEach.call(
      newRecord
        .querySelectorAll('[' + this.Constant_.DATA_ATTRIBUTE_REPLACE + ']'),
      function(element) {
        // Get object property to use
        var propertyName =
          element.getAttribute(this.Constant_.DATA_OBJECT_PROPERTY);

        if (typeof record[propertyName] === 'undefined') {
          element.classList.add('hidden');
        } else {
          // Get attribute to replace
          var attributeName =
            element.getAttribute(this.Constant_.DATA_ATTRIBUTE_REPLACE);
          // Replace object property value into corresponding attribute
          var value = record[propertyName];
          if (attributeName === 'src' || attributeName === 'href') {
            value = fieloUtils.decodeHTMLEntities(record[propertyName]);
          }
          // Guarda el valor
          element.setAttribute(attributeName, value);
          element.classList.remove('hidden');
        }
      },
     this
    );
  };

  //  Public methods

  /**
   * Renderiza los resultados
   * @param {array} records - Array con registros en formato json
   * @param {function} callback - funcin con mtodo a disparar cuando se termina la peticion
   */
  FieloRecordSet.prototype.render = function(records, callback) {
    //  Si NO tengo records
    if (typeof records.length === 'undefined' || records.length === 0) {
      // Deshabilita el contenido
      this.data_.classList.add(this.CssClasses_.DATA_DISABLED);
      //  Elimina los elementos de la lista
      fieloUtils.message.FieloMessage.clear();
      fieloUtils.message.FieloMessage.addMessages(
        fieloUtils.site.FieloSite.getLabel('noResultsFound')
      );
      fieloUtils.message.FieloMessage.setRedirect('#', 4);
      fieloUtils.message.FieloMessage.show();
      /*
       [].slice.call(this.data_.children).forEach(function(child) {
        child.parentNode.removeChild(child);
      });
      */
    } else {
      // Habilita el contenido
      this.data_.classList.remove(this.CssClasses_.DATA_DISABLED);
      // Acciones comunes a todas las vistas

      // Registro las propiedades de los records en un Array
      this.records_.fields = Object.keys(records[0]);

      // limpio el contenedor data
      this.cleanDataChilds_();

   // Preparo el Array donde se guardan los templates
      var newSetOfTemplates = [];

      // Preparo el Array donde se guardan los records por template
      var newSetOfRecords = [];

      // Cantidad de registros agregados en total
      var recordsAddedInTotal = 0;
      records.forEach(function(record) {
        // Cantidad de registros agregados por template
        var recordsAddedPerTemplate = 0;
        // Creo un record temporal
        var newRecord = this.recordTemplate_.cloneNode(true);

        this.setFieldsValues_(record, newRecord);

        this.setLinksToDetailValues_(record, newRecord);

        this.setSubcomponentsValues_(record, newRecord);

        newSetOfRecords.push(newRecord);
        recordsAddedInTotal += 1;

        newRecord = null;

        // Si el recordSetTemplate es el mismo nodo que el record
        if (!this.onlyOneTemplate_) {
          recordsAddedPerTemplate += 1;
          if (
            recordsAddedPerTemplate === this.records_.inTemplate ||
            records.length === recordsAddedInTotal
          ) {
            // Agrego los records al nuevo set de template (clonando)
            newSetOfTemplates.push(newSetOfRecords.slice(0));
            // limpio el set de records
            newSetOfRecords.length = 0;
            // llevo el contador a 0
            recordsAddedPerTemplate = 0;
          }
        }
      }, this);

      // Si el recordSetTemplate es el mismo nodo que el record
      if (this.onlyOneTemplate_) {
        // newSetOfRecords
        newSetOfRecords.forEach(
          function(record) {
            this.data_.appendChild(record);
          },
          this
        );
      } else {
        newSetOfTemplates.forEach(function(template) {
          var newTemplate = this.recordSetTemplate_.cloneNode(true);
          // borro todos los hijos de este nodo
          while (newTemplate.hasChildNodes()) {
            newTemplate.removeChild(newTemplate.firstChild);
          }

          template.forEach(
            function(record) {
              newTemplate.appendChild(record);
            },
            this
          );
          // Adjunto los template a data
          this.data_.appendChild(newTemplate);
        }, this);
      }

      // Termin de configurarlo, ahora lo inicializo en segundo plano
      setTimeout(function() {
        var records =
          this.element_.querySelectorAll('.' + this.CssClasses_.RECORD);
        componentHandler.upgradeElements(records);
        [].forEach.call(records, function(record) {
          record.FieloRecord.initForms();
        });
      }.bind(this), 0);
    }

    // llamo al callback
    if (callback) {
      callback(records);
    }
  };

  /**
   * Inicializa el elemento
   */
  FieloRecordSet.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
    }
  };

  //  El componente se registra por si solo.
  //  Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloRecordSet,
    classAsString: 'FieloRecordSet',
    cssClass: 'fielo-record-set'
  });
})();
