  /**
   * @description Clase constructora para el Site.
   * FieloSite muestra / oculta el spinner
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 1.0
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */

(function() {
  'use strict';

  var site = document.querySelector('.fielo-site');

  var FieloSite = {};
  site.FieloSite = FieloSite;
  FieloSite.element_ = site;

/**
   * Guarda strings para nombres de atributos data
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */

  FieloSite.DataAttribute_ = {
    // Nombre del prefijo
    PACKAGE_PREFIX: 'data-package-prefix',
    // Site Path
    SITE_PATH: 'data-sitepath'
  };

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */

  FieloSite.CssClasses_ = {
    // Nombre para ocultar el elemento
    PACKAGE_PREFIX: 'data-package-prefix'
  };

  /**
   * @method
   * @private
   * @summary Crea la url para el site config
   *
   * @return {string} Retorna la url para el siteConfig
   */

  FieloSite.requestToConfig_ = function() {
    // Construyo la url al SiteConfig
    var url = '/';
    if (this.sitePath_ !== '') {
      url += this.sitePath_ + '/';
    }
    if (this.ns !== '') {
      url += this.ns + '__';
    }
    url += 'SiteConfig';

    return url;
  };

  /**
   * Guarda en config las variables que vienen de la pgina SiteConfig
   * @param {HTMLelement} data - Data que contiene la configuracin
   * @param {Number} status - Status del AJAX
   */

  FieloSite.setConfig_ = function(data, status) {
    if (status === 200) {
      this.config = JSON.parse(data.innerHTML);
    } else {
      this.config = null;
    }
  };

  /**
   * Devuelve el label correspondiente
   * @param {String} label - label a pedir
   *
   * @return {String} Label
   */

  FieloSite.getLabel = function(label) {
    if (
      this.config !== null &&
      typeof this.config.label[label] !== 'undefined'
    ) {
      return this.config.label[label];
    }
    return label;
  };

  /**
   * Inicializa el elemento
   */
  FieloSite.init = function() {
    // Seteo el namespace
    this.ns = this.element_
      .getAttribute(this.DataAttribute_.PACKAGE_PREFIX);

    // Seteo el site path
    this.sitePath_ = this.element_
      .getAttribute(this.DataAttribute_.SITE_PATH);
    // Inicializo config_
    this.config = '';

    // Seteo la URL
    this.urlToConfig_ = this.requestToConfig_();

    // Peticin a la URL del site config y guarda la configuracin
    fieloUtils.ajax(this.urlToConfig_, false, this.setConfig_.bind(this));

    // Disponibiliza el site en fieloUtils
    fieloUtils.site = this.element_;
  };

  FieloSite.init();
})();

