(function() {
  'use strict';

  /**
   * @description Clase constructora del Footer.
   * FieloFooter se encarga de actualizar la altura a la cual empuja el footer
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @author Sebastian Claros <sebastian.claros@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloFooter = function FieloFooter(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloFooter = FieloFooter;

  // Properties

  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloFooter.prototype.Constant_ = {};

  /**
   * Guarda strings para nombres de clases definidas por este componente que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloFooter.prototype.CssClasses_ = {
    /** Elemento que empuja el footer */
    PUSHER: 'fielo-footer--is-pushing'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloFooter.prototype.setDefaults_ = function() {
    this.pusher_ = document.querySelector('.' + this.CssClasses_.PUSHER);
  };

  /**
   * Registra los listeners
   *
   * @private
   */
  FieloFooter.prototype.setListeners_ = function() {
    window.addEventListener('optimizedResize', this.resize.bind(this));
  };

  /**
   * Redimensiona el tamao del footer
   *
   * @private
   */

  FieloFooter.prototype.resize = function() {
    this.pusher_.setAttribute(
      'style',
      'height:' + this.element_.offsetHeight + 'px');
  };

  /**
   * Inicializa el elemento
   */
  FieloFooter.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.setListeners_();
      this.resize();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloFooter,
    classAsString: 'FieloFooter',
    cssClass: 'fielo-footer',
    widget: false
  });
})();
