(function() {
  'use strict';

  /**
   * @description Clase constructora del app menu.
   * FieloAppMenu activa las funcionalidades para el app menu
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 1
   * @author Nicolas Soberon <nicolas.soberon@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloAppMenu = function FieloAppMenu(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloAppMenu = FieloAppMenu;

  // Properties
  /**
   * Guarda las constantes en un lugar para que sean facilmente actualizadas
   * @enum {string | number}
   * @private
   */
  FieloAppMenu.prototype.Constant_ = {
  };

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   *
   * Esto nos permite cambiarlos solo en un lugar
   *
   * @enum {string}
   * @private
   */
  FieloAppMenu.prototype.CssClasses_ = {
    APP_MENU_CONTAINER: 'fielo-app-menu__body',
    APP_MENU_BTN: 'fielo-app-menu__btn',
    APP_MENU_BTN_OPEN: 'fielo-app-menu--is-open',
    APP_MENU_SLIDE_IN: 'fielo-app-menu--is-sliding-in',
    APP_MENU_SLIDE_OUT: 'fielo-app-menu--is-sliding-out'
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloAppMenu.prototype.setDefaults_ = function() {
    this.isOpen_ = false;
    this.toggleBtn_ =
      this.element_.querySelector('.' + this.CssClasses_.APP_MENU_BTN);
  };

  /**
   *
   * @param {HTMLelement} element - Elemento a mejorar
   * @private
   */
  FieloAppMenu.prototype.upgrade_ = function(element) {
    if (element.dataset.upgraded) {
      var elementUpgrades = element.dataset.upgraded.split(',').slice(1);
      elementUpgrades.forEach(function(upgrade) {
        componentHandler.upgradeElement(element, upgrade);
      });
    }
  };

  /**
   *
   *
   * @private
   */
  FieloAppMenu.prototype.menuBtn_ = function() {
    this.toggleBtn_.addEventListener('click', this.toggleMenu_.bind(this));
  };

  /**
   *
   *
   * @private
   */
  FieloAppMenu.prototype.toggleMenu_ = function() {
    this.element_.classList.toggle(this.CssClasses_.APP_MENU_BTN_OPEN);
    this.element_.classList.toggle(this.CssClasses_.APP_MENU_SLIDE_IN);
    if (this.isOpen_) {
      this.element_.classList.add(this.CssClasses_.APP_MENU_SLIDE_OUT);
    } else {
      this.element_.classList.remove(this.CssClasses_.APP_MENU_SLIDE_OUT);
    }
    // al cerrar actualizo el status
    this.isOpen_ = !this.isOpen_;
  };
  // Public methods

  /**
   * Recibe menu a agregar
   * @param {HTMLelement} element - Elemento a agregar
   * @private
   */
  FieloAppMenu.prototype.addToMenu = function(element) {
    this.upgrade_(element);
    this.element_.querySelector('.' + this.CssClasses_.APP_MENU_CONTAINER)
      .appendChild(element);
  };

  /**
   * Inicializa el elemento
   */
  FieloAppMenu.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      fieloUtils.appMenu = this.element_;
      this.menuBtn_();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloAppMenu,
    classAsString: 'FieloAppMenu',
    cssClass: 'fielo-app-menu',
    widget: true
  });
})();
