/**
 * @description Inicializa los mdulos viejos a deprecar
 *
 * @exports fielo
 * @author Sebastian Claros <sebastian.claros@fielo.com>
 * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
 * @version 1
  */
var oldLoader = (function() {
  'use strict';

  var modules = [];
  var eventListener = {
    DOMContentLoaded: [],
    DOMContentReloaded: [],
    AjaxBeforeSend: [],
    AjaxComplete: [],
    ObjectChange: []
  };

  /**
   * Registra Event Listeners
   * @param {string} eventName - evento a registrar: DOMContentLoaded, DOMContentReloaded, AjaxBeforeSend, AjaxComplete, ObjectChange
   * @param {Function} callback - funcin asociada al event listener
   */
  function addEventListener(eventName, callback) {
    // Si es un eventName nuevo lo crea
    if (typeof eventListener[eventName] === 'undefined') {
      eventListener[eventName] = [];
    }
    eventListener[eventName].push(callback);
  }

/**
 * Dispara el evento
 * @param  {string} event Tipo de evento a disparar
 */
  function dispatchEvent(event) {
    var eventName = event.type;
    if (typeof eventListener[eventName] === 'undefined') {
      throw new Error('Unkown event ' + eventName, 'oldLoader.js', 43);
    } else {
      for (var i = 0, max = eventListener[eventName].length; i < max; i++) {
        eventListener[eventName][i](event);
      }
    }
  }

  // Return an object exposed to the public
  return {

    /**
     * @method
     * @summary Agrega escuchador de eventos
     *
     * @param {string}                    eventName - Nombre del evento a escuchar
     * @param {callback} callback  - El callback que es asignado al evento registrado
     */
    addEventListener: addEventListener,

    /**
     * @method
     * @summary Interfaz provista para que los modulos puedan disparar
     * un evento, por ejemplo cuando se hace una recarga con Ajax se debe
     * disparar el evento DOMContentReloaded
     *
     * @param {string} event Nombre del evento a disparar
     * @throws {string} Tira un error si el nombre del evento es desconocido
     * @return {string} Atributo data-package-prefix del nodo HTML
     */
    dispatchEvent: dispatchEvent,

     /**
       * @method
       * @summary Dispara un evento
       *
       * @param {DOMelement} element   Elemento HTML sobre el cual se dispar el evento
       * @param {string} eventName Evento a disparar
       */
    dispatchElementEvent: function(element, eventName) {
      if ('createEvent' in document) {
        var evt = document.createEvent('HTMLEvents');
        evt.initEvent(eventName, false, true);
        element.dispatchEvent(evt);
      } else {
        element.fireEvent('on' + eventName);
      }
    },

    /**
     * @method
     * @summary inicializa los formularios dentro de un elemento dado
     *
     * @param {HTMLelement} element - elemento a buscar formularios
     * @todo Verificar si ya esta cargado
     * @todo Verificar las dependencias
     */

    initForms_: function(element) {
      oldLoader.dispatchEvent({
        type: 'DOMContentReloaded',
        target: element,
        module: 'form'
      });
    },

    /**
     * @method
     * @summary Carga el modulo en fielo y lo inicializa
     *
     * @param {object} module - mdulo a inicializar
     * @todo Verificar si ya esta cargado
     * @todo Verificar las dependencias
     */
    loadModule: function(module) {
      // Agrega a la lista
      modules.push(module);
      // Crea un alias
      this[module.getName()] = modules[modules.length - 1];

      // Lo inicializa
      if (typeof module.initialize !== 'undefined') {
        module.initialize();
      }
    },
    eventListener: eventListener

  };
})();

