(function() {
  'use strict';

  /**
   * @description Clase constructora del Slider
   * FieloSlider activa las funcionalidades para el slider
   * Implementa los patrones de diseo definidos por MDL en
   * {@link https://github.com/jasonmayes/mdl-component-design-pattern}
   *
   * @version 2
   * @author Hugo Gmez Mac Gregor <hugo.gomez@fielo.com>
   * @param {HTMLElement} element - Elemento que ser mejorado.
   * @constructor
   */
  var FieloSlider = function FieloSlider(element) {
    this.element_ = element;

    // Initialize instance.
    this.init();
  };
  window.FieloSlider = FieloSlider;

  // Properties

  /**
   * Guarda strings para nombres de clases definidas por este componente
   * que son usadas por JavaScript.
   * Esto nos permite cambiarlos solo en un lugar
   * @enum {string}
   * @private
   */
  FieloSlider.prototype.CssClasses_ = {
    // Nombre para activar la animacion
    SLIDING: 'fielo-slider__container--is-sliding',
    // Flecha anterior
    ARROW_PREVIOUS: 'fielo-slider__arrow--is-previous',
    // Flecha siguiente
    ARROW_NEXT: 'fielo-slider__arrow--is-next',
    // elemento que contiene las slides
    CONTAINER: 'fielo-slider__container',
    // elemento que representa un slide
    SLIDES: 'fielo-slider__slide'
  };

  /**
   * Setea estilos en el atributo styles
   *
   * @param {HTMLelement} element - element al cual aplicar los estilos
   * @param {object} styles - reglas que se van a aplicar
   * @private
   */
  FieloSlider.prototype.setStyles_ = function(element, styles) {
    // generar descriptor de reglas
    var concatenatedStyles = '';
    for (var property in styles) {
      if ({}.hasOwnProperty.call(styles, property)) {
        concatenatedStyles += styles[property];
      }
    }
    element.setAttribute('style', concatenatedStyles);
  };

  // Private methods

  /**
   * Setea los registros por defecto
   *
   * @private
   */
  FieloSlider.prototype.setDefaults_ = function() {
    // Slide que se est mostrando
    this.currentSlide_ = 1;
    // Contenedor de Slides
    this.container_ =
      this.element_.querySelector('.' + this.CssClasses_.CONTAINER);
    this.slides_ =
    // Slides existentes
      this.container_.querySelectorAll('.' + this.CssClasses_.SLIDES);
    // Cantidad de slides
    this.quantity_ = this.slides_.length;
    // Ancho del slider
    this.width_ = this.element_.offsetWidth;
    // flechas
    this.arrow_ = {};
    this.arrow_.previous =
      this.element_.querySelector('.' + this.CssClasses_.ARROW_PREVIOUS);
    this.arrow_.next =
      this.element_.querySelector('.' + this.CssClasses_.ARROW_NEXT);
    this.setKeyframeRule_();
    // Objeto con los estilos actualizados
    this.containerStyles_ = {};
    // Estado de reproduccin
    this.play_ = true;
    // Segundos a esperar por slide
    this.delay_ = 3;
  };

  /**
   * Registra la regla keyframe de css para animacin
   *
   * @private
   */
  FieloSlider.prototype.setKeyframeRule_ = function() {
    this.keyframes_ = [];
    [].forEach.call(document.styleSheets, function(sheet) {
      [].forEach.call(sheet.cssRules, function(rule) {
        if (rule.type === 7 && rule.name === 'slide') {
          this.keyframes_.push(rule);
        }
      }, this);
    }, this);
  };

  /**
   * Registra los listeners de las animaciones
   *
   * @private
   */
  FieloSlider.prototype.setAnimationListeners_ = function() {
    this.element_.addEventListener(
      'animationstart', this.animationStarted.bind(this)
    );
    this.element_.addEventListener(
      'animationiteration', this.animationNewIteration
    .bind(this));
    this.element_.addEventListener(
      'animationend', this.animationEnded.bind(this)
    );
  };

  /**
   * Setea / Actualiza los keyframes
   *
   * @param {string} name - Nombre de la animacin
   * @param {string} properties - propiedades de la animacin
   * @private
   */
  FieloSlider.prototype.setKeyframe_ = function(name) {
    this.keyframes_.forEach(function(rule) {
      if (rule.name === name) {
        rule.cssRules[0].style.left =
          '-' + (this.currentSlide_ - 1) * 100 + '%';
      }
    }.bind(this));
  };

  /**
   * Setea el ancho del container
   *
   * @private
   */
  FieloSlider.prototype.setContainerWidth_ = function() {
    this.containerStyles_.width = 'width:' + (this.quantity_ * 100) + '%;';
    this.setStyles_(this.container_, this.containerStyles_);
  };

  /**
   * Setea la posicin del container
   *
   * @private
   */
  FieloSlider.prototype.saveCurrentPosition_ = function() {
    this.containerStyles_.left =
      'left:-' + ((this.currentSlide_ - 1) * 100) + '%;';
    this.setStyles_(this.container_, this.containerStyles_);
  };

  /**
   * Setea las propiedades de cada slide
   *
   * @private
   */
  FieloSlider.prototype.setSlidesProperties_ = function() {
    [].forEach.call(this.slides_, function(slide) {
      this.setSlideLinks_(slide);
      this.setSlidesWidth_(slide);
    }, this);
  };

  /**
   * Setea el ancho de los slides
   * @param {HTMLelement} slide - slide a la cual se le setea el ancho
   * @private
   */
  FieloSlider.prototype.setSlidesWidth_ = function(slide) {
    slide.setAttribute('style', 'width: calc(100%/' + this.quantity_ + ');');
  };

  /**
   * Registra los links a los slides
   * @param {HTMLelement} slide - slide a la cual se le setea un link
   * @private
   */
  FieloSlider.prototype.setSlideLinks_ = function(slide) {
    var url = slide.getAttribute('data-link');
    if (url !== '') {
      slide.addEventListener('click', function() {
        window.location = url;
      });
    }
  };

  /**
   * Registra la navegacin del slider
   * @private
   */
  FieloSlider.prototype.registerNavigation_ = function() {
    this.arrow_.previous.addEventListener('click', this.goPrevious.bind(this));
    this.arrow_.next.addEventListener('click', this.goNext.bind(this));
  };

  // Public methods

  /**
   * Mtodo que se ejecuta cuando arranca la animacin
   *
   * @public
   */
  FieloSlider.prototype.animationStarted = function() {
  };

  /**
   * Mtodo que se ejecuta cuando arranca una iteracin
   *
   * @public
   */
  FieloSlider.prototype.animationNewIteration = function() {
  };

  /**
   * Mtodo que se ejecuta cuando arranca termina una animacin
   *
   * @param {event} e - Evento de animacin
   * @public
   */
  FieloSlider.prototype.animationEnded = function(e) {
    this.saveCurrentPosition_();
    switch (e.animationName) {
      case 'slide':
        this.container_.classList.remove(this.CssClasses_.SLIDING);
        break;
      default:
        break;
    }
    if (this.play_) {
      setTimeout(this.goToSlide.bind(this), this.delay_ * 1000);
    }
  };

  /**
   * Muestra el slide anterior
   *
   * @public
   */
  FieloSlider.prototype.goPrevious = function() {
    if (this.currentSlide_ > 1) {
      this.goToSlide(this.currentSlide_ - 1);
    }
  };

  /**
   * Muestra el slide siguiente
   *
   * @public
   */
  FieloSlider.prototype.goNext = function() {
    if (this.currentSlide_ < this.quantity_) {
      this.goToSlide(this.currentSlide_ + 1);
    }
  };

  /**
   * Setea el play
   *
   * @param {bool} boolean - Verdadero para play
   * @public
   */
  FieloSlider.prototype.setAutoPlay = function(boolean) {
    this.play_ = boolean;
    if (this.play_) {
      this.goToSlide();
    }
  };

  /**
   * Va a la slide correspondiente
   *
   * @param {number} slide - slide que se quiere mostrar
   * @public
   */
  FieloSlider.prototype.goToSlide = function(slide) {
    if (slide) {
      this.currentSlide_ = slide;
    } else if (this.currentSlide_ < this.quantity_) {
      this.currentSlide_ ++;
    } else {
      this.currentSlide_ = 1;
    }

    this.setKeyframe_('slide');
    this.container_.classList.add(this.CssClasses_.SLIDING);
  };

  /**
   * Inicializa el elemento
   *
   * @public
   */
  FieloSlider.prototype.init = function() {
    if (this.element_) {
      this.setDefaults_();
      this.setAnimationListeners_();
      this.setContainerWidth_();
      this.setSlidesProperties_();
      this.registerNavigation_();
      this.saveCurrentPosition_();
    }
  };

  // El componente se registra por si solo.
  // Asume que el componentHandler esta habilitado en el scope global
  componentHandler.register({
    constructor: FieloSlider,
    classAsString: 'FieloSlider',
    cssClass: 'fielo-slider',
    widget: true
  });
})();
